.pagination {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  list-style: none;
  padding-left: 0;
  width: 100%;

  &__item {
    display: flex;
    justify-content: center;
    padding: 0 5px;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    font-weight: 500;
    text-decoration: none;
    color: $color-black-primary;
    background-color: $color-gray-decor;
    transition: background-color 0.2s;

    @include desktop {
      &:hover,
      &:focus {
        background-color: $color-accent-yellow;
        transition: background-color 0.2s;
      }
    }

    &--prev,
    &--next {
      width: 200px;
      background-color: transparent;

      @include desktop {
        &:hover,
        &:focus {
          background-color: transparent;
        }
      }
    }

    &--is-active {
      background-color: $color-accent-yellow;
    }
  }

  &__dots {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    font-weight: 500;
    text-decoration: none;
    background-color: transparent;
  }
}
