.contacts {
  position: relative;
  padding-bottom: 60px;

  .callback-form {
    margin-top: 180px;
  }

  &__top {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    padding-top: 38px;
  }

  &__bottom {
    margin: 0 auto;
    padding-top: 50px;
    max-width: 720px;
    text-align: center;

    h5 {
      margin-top: 0;
      margin-bottom: 20px;
    }

    p {
      margin-bottom: 22px;
    }

    .contacts-us__video {
      padding-bottom: 48%;
      width: 100%;
    }
  }
}

.contacts-info {
  width: calc(100% - 570px);

  .section-title {
    padding-top: 0;
    text-align: left;
  }

  &__list {
    display: flex;
    flex-flow: row nowrap;
    list-style: none;
    padding-left: 0;

    &-item {
      margin-bottom: 22px;
      width: 50%;
      line-height: 20px;

      strong {
        display: block;
        margin-bottom: 12px;
      }

      span {
        display: block;
      }

      a {
        display: block;
        text-decoration: none;
        color: $color-black-primary;
      }
    }
  }

  &__social {
    display: flex;
    align-items: center;

    strong {
      margin-right: 15px;
    }

    &-link {
      display: block;
      margin-right: 10px;
      width: 35px;
      height: 35px;

      &--inst {
        border-radius: 50%;
        padding: 6px;
        background-image:
          radial-gradient(
            farthest-corner at -6px 20px,
         #fed576 0%,
         #f47133 26.34%,
         #bc3081 60.91%,
         #b424a6 100%
          );

        svg {
          fill: $color-white;
        }
    }
    }
  }

  &__footer {
    padding-top: 15px;
    padding-right: 75px;
    font-size: $fz-small;
    line-height: 17px;
  }
}

.contacts-map {
  width: 570px;
  height: 400px;
}