.service {
  .section-title {
    padding-top: 40px;

    &__title {
      margin-bottom: 25px;
      text-align: left;
    }
  }

  &__subtitle {
    margin-bottom: 65px;
    max-width: 65%;
    line-height: 24px;
  }

  &__figure {
    display: flex;
    flex-flow: row nowrap;
    margin: 0;
    padding-bottom: 35px;

    img {
      width: 100%;
      max-width: 570px;
    }

    figcaption {
      padding-left: 30px;
      width: calc(100% - 570px);
    }

    h5 {
      margin-top: 0;
      margin-bottom: 20px;
    }

    ul {
      list-style: none;
      margin: 0;
      padding-left: 0;

      li {
        position: relative;
        padding-left: 20px;
        line-height: 24px;

        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 6px;
          width: 8px;
          height: 8px;
          background-color: $color-accent-yellow;
        }

        &:not(:last-of-type) {
          margin-bottom: 8px;
        }
      }
    }
  }
}

.service-description {
  display: flex;
  flex-flow: row nowrap;
  padding-top: 20px;
  padding-bottom: 15px;

  &__gallery {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    width: 370px;
  }

  &__pic {
    margin-bottom: 20px;
    width: 175px;
    height: 175px;
  }

  &__text {
    padding-left: 30px;
    width: calc(100% - 370px);

    h5 {
      margin-top: 5px;
      margin-bottom: 20px;
    }

    p {
      margin-top: 0;
      margin-bottom: 25px;
      line-height: 24px;
    }
  }
}

.service-row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.service-article {
  padding-right: 30px;
  padding-left: 15px;
  line-height: 25px;
  letter-spacing: 0.08em;

  h5 {
    margin-bottom: 25px;
    letter-spacing: normal;
  }

  &:first-child {
    padding-left: 0;
  }
}

.service-result {
  position: relative;
  display: flex;
  flex-flow: row wrap;
  margin-top: 85px;
  margin-bottom: 120px;
  padding: 15px;
  padding-bottom: 55px;
  background-color: $color-gray-decor;

  &::before {
    @include triangle(to top left, 15px, 15px, $color-white);

    content: "";
    position: absolute;
    left: 0;
    top: 0;
  }

  &::after {
    @include triangle(to bottom right, 50px, 50px, $color-white);

    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
  }

  h5 {
    margin-top: 0;
    margin-bottom: 25px;
    width: 100%;
    text-align: center;
  }

  ul {
    width: 33.33%;
  }
}

.service-footer {
  padding-top: 20px;
  padding-bottom: 100px;

  h4 {
    margin-top: 0;
    margin-bottom: 25px;
    text-align: center;
  }
}
