.articles {
  padding-bottom: 85px;

  &__wrapper {
    display: flex;
    flex-flow: row wrap;
  }
}

.articles-item {
  margin-bottom: 38px;
  width: 560px;

  &__pic {
    margin-bottom: 20px;
    width: 560px;
    height: 295px;

    img {
      height: auto;
      max-width: 100%;
    }
  }

  &__date {
    margin-bottom: 15px;
    font-size: 14px;
    line-height: 17px;
    color: #999;
  }

  &__name {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 18px;
    line-height: 22px;
  }

  &__description {
    margin-top: 0;
    margin-bottom: 18px;
    line-height: 20px;
  }

  .btn {
    padding: 22px 28px;
    font-size: 16px;
  }

  &:nth-of-type(2n) {
    margin-left: auto;
  }
}
