@mixin desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin media($width, $option: 0) {
  @if ($option == 0) {
    @media (max-width: $width) {
      @content;
    }
  } @else if $option == 1 {
    @media (min-width: $width + 1px) {
      @content;
    }
  }
}
