.main-header {
  background-color: $color-white;

  &__top {
    padding-top: 16px;
    padding-bottom: 16px;

    .container {
      display: flex;
      flex-flow: row nowrap;
      align-items: flex-start;
    }
  }

  &__nav {
    background-color: $color-black;

    .container {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
    }
  }

  &-logo {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 276px;
    text-decoration: none;
    transition: opacity 0.2s;

    &__description {
      margin-left: 10px;
      width: 145px;
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;
      color: $color-black;
    }

    @include desktop {
      &:hover,
      &:focus {
        opacity: 0.8;
        transition: opacity 0.2s;
      }
    }
  }

  &-address {
    list-style: none;
    margin: 0;
    margin-left: 25px;
    padding-top: 5px;
    padding-left: 0;
    width: 340px;
    font-size: $fz-small;

    li {
      position: relative;
      padding-left: 25px;

      &:first-of-type {
        margin-bottom: 30px;
      }
    }

    svg {
      position: absolute;
      left: 0;
      top: 0;
      width: 18px;
      height: 18px;
    }
  }
}

.main-header-social {
  margin-left: 55px;
  width: 290px;
  font-size: $fz-small;

  &__header {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
  }

  &__footer {
    margin-bottom: 0;

    svg {
      display: inline-block;
      vertical-align: middle;
      margin-left: 5px;
      width: 84px;
      height: 18px;
    }

    span {
      display: block;
      padding-top: 8px;
      width: 100%;
    }
  }

  &__link {
    margin-left: 10px;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    text-decoration: none;

    &--inst {
      padding: 5px;
      background-image:
        radial-gradient(
          farthest-corner at -6px 20px,
          #fed576 0%,
          #f47133 26.34%,
          #bc3081 60.91%,
          #b424a6 100%
        );

      svg {
        fill: $color-white;
      }
    }
  }

  p {
    margin-top: 0;
  }
}

.main-header-callback {
  padding-top: 5px;
  width: 195px;
  text-align: right;

  a {
    display: inline-block;
    margin-bottom: 12px;
    font-weight: 700;
    font-size: 18px;
    text-decoration: none;
    color: $color-black;
  }

  button {
    margin-left: auto;
  }
}

.main-header-list {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;

  &__item {
    &:first-of-type {
      .main-header-list__link {
        padding-left: 0;
      }
    }

    &:not(:last-of-type) {
      margin-right: 56px;
    }
  }

  &__link {
    display: block;
    padding: 16px 10px;
    font-weight: 500;
    text-decoration: none;
    color: $color-white;
  }
}

.main-header-search {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  padding: 10px 0;
  width: 100%;
  max-width: 315px;

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    width: 32px;
    height: 32px;
    background-color: $color-accent-yellow;
    cursor: pointer;

    svg {
      width: 18px;
      height: 18px;
    }
  }

  &__field {
    border: none;
    padding: 8px 10px;
    font-size: 12px;
    width: 100%;
    max-width: 282px;
  }
}
