.about {
  position: relative;
  padding-bottom: 80px;

  .section-title {
    padding-top: 40px;
    text-align: left;
  }

  .services-grid {
    justify-content: flex-start;

    &__item {
      width: 210px;

      &:not(:nth-of-type(5n)) {
        margin-right: 30px;
      }

      &:last-of-type {
        justify-content: space-between;
      }
    }

    &__bg-num {
      position: absolute;
      left: 50%;
      top: 50%;
      font-weight: 800;
      font-size: 100px;
      color: $color-white;
      transform: translate(-50%, -50%);
    }

    &__name {
      position: relative;
      z-index: 1;
      padding: 0 25px;
      font-size: 16px;
      line-height: 24px;
    }
  }
}

.about-info {
  display: flex;
  flex-flow: row nowrap;

  &__gallery {
    width: 570px;

    &-slider-big {
      .about-info__gallery-pic {
        width: 570px;
        height: 355px;
      }
    }

    &-slider-small {
      .about-info__gallery-pic {
        border: 2px solid transparent;
        width: 110px !important;
        height: 100px;
        box-sizing: border-box;
        cursor: pointer;
      }

      .slick-current {
        .about-info__gallery-pic {
          border: 2px solid $color-accent-yellow;
        }
      }
    }

    &-slider-controls {
      position: relative;
      padding-top: 12px;
      padding-right: 45px;
      padding-left: 45px;
    }

    &-pic {
      position: relative;
      overflow: hidden;

      img {
        position: absolute;
        left: 50%;
        top: 50%;
        width: auto;
        height: auto;
        min-width: 100%;
        min-height: 100%;
        transform: translate(-50%, -50%);
      }
    }
  }

  &__text {
    padding-left: 30px;
    width: calc(100% - 570px);

    p {
      margin-top: 0;
      margin-bottom: 21px;
      line-height: 20px;
    }
  }

  &__arrow {
    position: absolute;
    top: 50%;
    z-index: 2;
    width: 14px;
    height: 21px;
    transform: translateY(-50%);
    cursor: pointer;

    &--prev {
      right: 5px;
    }

    &--next {
      left: 5px;
    }
  }
}

.about-advantages {
  padding-top: 75px;

  &__header {
    padding-bottom: 18px;

    h5 {
      margin-top: 0;
      margin-bottom: 25px;
      text-align: center;
    }

    p {
      margin-top: 0;
      padding: 0 16%;
      text-align: center;
    }
  }

  &__wrapper {
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    margin-bottom: 30px;
    padding-top: 13px;
    padding-bottom: 14px;
    width: 370px;
    height: 87px;

    &::before {
      content: "";
      position: absolute;
      left: 8%;
      top: 0;
      width: 84%;
      height: 100%;
      background-color: $color-gray-decor;
      transform: skewX(-35deg);
    }

    svg {
      position: absolute;
      left: 25px;
      top: 7px;
      width: 30px;
      height: 30px;
    }

    p {
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
      overflow: hidden;
      margin: 0;
      width: 250px;
      height: 60px;
      line-height: 20px;
    }
  }
}

.about-services {
  margin-bottom: 52px;
  padding-top: 48px;

  h5 {
    margin-top: 0;
    margin-bottom: 0;
    padding: 0 30%;
    text-align: center;
  }
}

.about-conveniences {
  position: relative;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  margin-bottom: 85px;
  padding: 30px;
  background-color: $color-gray-decor;

  &::before {
    @include triangle(to top left, 50px, 50px, $color-white);

    content: "";
    position: absolute;
    left: 0;
    top: 0;
  }

  &::after {
    @include triangle(to bottom right, 50px, 50px, $color-white);

    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
  }

  h5 {
    margin-top: 0;
    width: 100%;
    text-align: center;
  }

  &__list {
    width: 30%;

    li {
      line-height: 20px;

      &::before {
        top: 4px;
        width: 10px;
        height: 10px;
      }

      &:not(:last-of-type) {
        margin-bottom: 30px;
      }
    }
  }
}

.about-team {
  display: flex;
  flex-flow: row wrap;

  h5 {
    margin-top: 0;
    width: 100%;
    text-align: center;
  }
}

.about-team-item {
  margin-bottom: 30px;
  padding-bottom: 20px;
  width: 270px;
  text-align: center;
  background-color: $color-gray-decor;

  &:not(:nth-of-type(4n)) {
    margin-right: 30px;
  }

  &__name {
    display: block;
    margin-top: 16px;
    margin-bottom: 12px;
    font-weight: 600;
    font-size: $fz-small;
    line-height: 17px;

    span {
      display: block;
    }
  }

  &__position {
    display: block;
    font-size: $fz-small;
    line-height: 17px;
  }

  &__pic {
    position: relative;
    width: 270px;
    height: 255px;

    img {
      position: absolute;
      left: 50%;
      top: 50%;
      min-width: 100%;
      min-height: 100%;
      transform: translate(-50%, -50%);
    }
  }
}