.field {
  position: relative;
  width: 100%;
  max-width: 280px;

  &__input {
    border-radius: none;
    border: 1px solid rgba($color-black, 0.2);
    padding: 22px 15px;
    width: 100%;
    font-size: $fz-small;
    background-color: $color-gray-decor;

    &::placeholder {
      color: #999;
    }

    &.isError {
      border: 1px solid $color-accent;
    }
  }

  textarea {
    resize: none;
  }
}