*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  box-sizing: border-box;
  font-family: $font-default;
  color: $color-black-primary;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

.wrapper {
  border-right: 50px solid $color-gray-decor;
  border-left: 50px solid $color-gray-decor;
  min-height: 100vh;
}

h1 {
  font-size: $fz-h1;
}

h2 {
  font-size: $fz-h2;
}

h3 {
  font-size: $fz-h3;
}

h4 {
  font-size: $fz-h4;
}

h5 {
  font-size: $fz-h5;
}

h6 {
  font-size: $fz-h6;
}

a {
  color: $color-accent-yellow;

  &:hover,
  &:focus {
    text-decoration: none;
  }
}

img {
  height: auto;
  max-width: 100%;
}

table {
  margin: 0 auto;
  border-collapse: collapse;
  width: 100%;
  max-width: 900px;
  font-size: 14px;
  line-height: 17px;

  a {
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: $color-black-primary;
  }

  small {
    font-size: 12px;
    line-height: 15px;
  }

  thead {
    th {
      padding: 10px 20px;
      font-weight: 700;
      text-align: left;
      background-color: $color-accent-yellow;

      &:not(:last-of-type) {
        border-right: 4px solid $color-white;
      }
    }
  }

  tbody {
    td {
      empty-cells: show;
      border-collapse: collapse;
      padding: 10px 20px;
      padding-bottom: 11px;
      background-color: $color-gray-decor;

      &:empty {
        background-color: $color-gray-decor;
      }

      &:not(:last-of-type) {
        border-right: 4px solid $color-white;
      }
    }

    tr {
      &:first-of-type {
        td {
          border-right: none;
          padding: 2px 20px;
        }
      }

      &:nth-of-type(2n) {
        td {
          background-color: $color-white;
        }
      }
    }
  }
}
