.btn {
  border: none;
  padding: 13px 22px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  color: $color-white;
  background-color: $color-accent;
  transition: background-color 0.2s;
  cursor: pointer;

  @include desktop {
    &:hover,
    &:focus {
      background-color: $color-black-primary;
      transition: background-color 0.2s;
    }
  }
}
