.main-footer {
  position: relative;
  padding: 50px 0;
  color: $color-gray-decor;
  background-color: $color-black-primary;

  &__up-btn {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    padding-top: 50px;
    width: 50px;
    height: 100%;
    text-decoration: none;
    background-color: $color-accent;

    svg {
      width: 14px;
      height: 9px;
    }
  }

  &__wrap {
    display: grid;
    grid-template-columns: 335px 465px 370px;
  }

  &__logo {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
    margin-bottom: 25px;
    font-size: $fz-small;
    text-decoration: none;
    color: $color-white;

    .sprite-logo_footer {
      margin-top: 7px;
      margin-right: 10px;
    }

    &-description {
      width: 145px;
      max-height: 55px;
      line-height: 18px;
    }
  }

  &__copy {
    margin-top: 0;
    margin-bottom: 22px;
    font-size: $fz-small;
  }

  &__about {
  }

  &__nav {
    display: flex;
    flex-flow: row nowrap;
    padding-top: 2px;
  }

  &__info {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    padding-top: 2px;

    svg {
      width: 15px;
      height: 15px;
    }
  }
}

.main-footer-nav-list {
  list-style: none;
  margin: 0;
  padding-left: 0;

  &:first-of-type {
    margin-right: 82px;
  }

  ul {
    list-style: none;
    margin-top: 20px;
    padding-left: 0;

    li {
      position: relative;
      padding-left: 14px;

      &::before {
        content: "";
        position: absolute;
        left: 1px;
        top: 50%;
        width: 3px;
        height: 3px;
        background-color: $color-accent-yellow;
        transform: translateX(-50%);
      }
    }

    a {
      font-weight: 400;
      color: rgba($color: $color-gray-decor, $alpha: 0.8);
    }
  }

  li:not(:last-of-type) {
    margin-bottom: 20px;
    line-height: 20px;
  }

  a {
    display: block;
    font-weight: 600;
    text-decoration: none;
    color: $color-gray-decor;
  }
}

.main-footer-develop {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-end;

  &__logo {
    font-size: 12px;
    text-decoration: none;
    color: rgba($color: $color-gray-decor, $alpha: 0.5);

    span {
      display: block;
      margin-bottom: 4px;
    }
  }

  &__link {
    margin-left: 22px;
    font-size: 12px;
    line-height: 15px;
    text-decoration: none;
    color: rgba($color: $color-gray-decor, $alpha: 0.69);
  }
}

.main-footer-widget {
  margin-top: 5px;
  width: 142px;
  height: 112px;
}

.main-footer-contacts {
  width: calc(100% - 142px);
  text-align: right;

  h6 {
    margin-top: 0;
    margin-bottom: 4px;

    > a {
      font-size: 18px;
      text-decoration: none;
      color: $color-gray-decor;
    }
  }

  a {
    font-size: 14px;
  }

  ul {
    list-style: none;
    margin-top: 45px;
    margin-bottom: 0;
    padding-left: 0;
  }

  li {
    font-size: 14px;

    &:not(:last-of-type) {
      margin-bottom: 25px;
    }

    svg {
      margin-right: 10px;
    }
  }
}