.article-page {
  padding-bottom: 100px;

  p {
    margin-top: 0;
    margin-bottom: 24px;
    line-height: 20px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  > .container {
    max-width: 770px;
  }

  .section-title {
    padding-top: 40px;

    &__title {
      font-size: 28px;
      text-align: left;
    }
  }
}

.article-ilustration {
  margin: 0;
  margin-bottom: 24px;

  figcaption {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    padding-top: 16px;
    font-size: 14px;
    line-height: 17px;
    color: #999;
  }
}

.article-title {
  margin-top: 32px;
  margin-bottom: 20px;
}

.article-back-btn {
  display: inline-block;
  margin-top: 32px;
  font-size: 16px;
  line-height: 20px;
  color: $color-black-primary;
}
