.is-hidden {
  // stylelint-disable-next-line declaration-no-important
  display: none !important;
}

.for-desktop {
  @include mobile {
    // stylelint-disable-next-line declaration-no-important
    display: none !important;
  }
}

.for-mobile {
  @include desktop {
    // stylelint-disable-next-line declaration-no-important
    display: none !important;
  }
}

.clearfix {
  overflow: auto;

  &::after {
    content: '';
    display: table;
    clear: both;
  }
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: $width-container;
}

.section-title {
  position: relative;
  padding-top: 75px;
  text-align: center;

  &__title {
    margin-top: 0;
    margin-bottom: 30px;
    font-size: $fz-section-title;
  }

  &__subtitle {
    position: absolute;
    left: 50%;
    bottom: 0;
    z-index: -1;
    margin: 0;
    font-family: $font-title;
    font-size: 54px;
    text-transform: uppercase;
    color: $color-gray-decor;
    transform: translateX(-50%);
  }

  &--inner {
    padding-top: 40px;
    text-align: left;
  }
}

.accent-color {
  color: $color-accent;
}

.styled-list {
  list-style: none;
  margin: 0;
  padding-left: 0;

  li {
    position: relative;
    padding-left: 20px;
    line-height: 24px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 6px;
      width: 8px;
      height: 8px;
      background-color: $color-accent-yellow;
    }

    &:not(:last-of-type) {
      margin-bottom: 8px;
    }
  }

  &--ok {
    li {
      &::before {
        width: 12px;
        height: 10px;
        background: url('../images/sprites/svg/list-check.svg') 0 0 no-repeat;
        background-size: cover;
      }
    }
  }
}
