.breadcrumbs {
  border-bottom: 2px solid $color-gray-decor;

  &__list {
    display: flex;
    flex-flow: row nowrap;
    list-style: none;
    margin: 0;
    padding: 0;
    padding-top: 20px;
    width: 100%;
  }

  &__link {
    font-size: 12px;
    text-decoration: none;
    color: $color-black-primary;
  }

  &__item {
    padding-bottom: 5px;
    font-size: 12px;
    color: $color-black-primary;

    &:not(:last-of-type) {
      margin-right: 6px;

      &::after {
        content: ">";
        margin-left: 6px;
        font-size: 12px;
        color: $color-accent-yellow;
      }
    }
  }

  &__last {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -6px;
      width: 100%;
      height: 2px;
      background-color: $color-accent-yellow;
    }
  }
}