.modal {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  background: rgba(25, 25, 25, 0.1);

  &__container {
    position: relative;
    z-index: 1;
    border-top: 12px solid $color-accent-yellow;
    padding: 24px 30px;
    padding-top: 18px;
    width: 100%;
    max-width: 330px;
    box-shadow: 0px 24px 40px rgba(0, 0, 0, 0.12);
    transform: translateY(-200%);
    background:
      url('../images/mcallback_bg_1.png') top left no-repeat,
      url('../images/mcallback_bg_2.png') bottom right no-repeat;
    background-color: $color-white;
    transition: transform 0.4s;

    .btn {
      margin-bottom: 10px;
      padding: 22px;
      width: 100%;
      font-size: 16px;
    }

    .field {
      display: block;
      margin-bottom: 20px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-size: 22px;
    line-height: 27px;
    text-align: center;
  }

  &__footer {
    font-size: 10px;
    line-height: 12px;
    text-align: center;
    color: #999;

    a {
      color: #999;
    }
  }

  &__inner {
    display: flex;
    flex-flow: row nowrap;
  }

  &__col {
    &--left {
      margin-right: 30px;
      max-width: 270px;

      .field {
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }

    &--right {
      width: 100%;
      max-width: 380px;

      .field__input {
        height: 100%;
      }
    }
  }

  &--review {
    .modal__container {
      max-width: 730px;
    }

    .modal__footer {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
      justify-content: space-between;
      padding-top: 20px;
      padding-bottom: 20px;

      span {
        width: 190px;
        text-align: left;
      }

      .btn {
        margin-bottom: 0;
        max-width: 270px;
      }
    }
  }

  &--thx {
    .modal__container {
      display: flex;
      flex-flow: column nowrap;
      justify-content: flex-end;
      min-height: 420px;
    }

    .modal__title {
      margin-bottom: 50%;
    }
  }

  &.js-modal-is-showed {
    z-index: 10000;
    opacity: 1;
    visibility: visible;
    transition: opacity 0.5s;

    .modal__container {
      transform: translateY(0);
      transition: transform 0.4s;
    }
  }
}
