//
// INTRO BLOCK
//

.intro {
  position: relative;
  max-height: 600px;

  &::before {
    content: "";
    position: absolute;
    left: -20%;
    top: 0;
    z-index: 10;
    width: 65.5%;
    height: 100%;
    background-color: rgba($color-gray-decor, 0.9);
    transform: skewX(-35deg);
  }
}

.intro-controls {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;

  &__arrow {
    position: absolute;
    top: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: -30px;
    border: 10px solid $color-gray-decor;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    background-color: $color-white;
    transition: border-width 0.3s;
    cursor: pointer;

    svg {
      width: 22px;
      height: 14px;
    }

    &--prev {
      left: -30px;
      z-index: 10;
    }

    &--next {
      right: -30px;
      z-index: 10;
    }

    @include desktop {
      &:hover,
      &:focus {
        border-width: 5px;
        transition: border-width 0.3s;
      }
    }
  }
}

.intro-slider {
  &__slide {
    width: 100%;
    max-height: 600px;

    img {
      height: auto;
      max-width: 100%;
    }
  }
}

.intro-slider-text {
  position: absolute;
  left: 50%;
  top: 0;
  z-index: 11;
  padding-top: 80px / 1920px * 100vw;
  transform: translateX(-50%);

  .slick-track {
    margin-left: 0;
  }

  &__slide {
    padding-right: 60%;
    width: 100%;

    h1 {
      margin-bottom: 20px;
      line-height: 59px;
    }

    ul {
      margin-top: 0;
      margin-bottom: 40px;
      padding-left: 0;
    }

    li {
      position: relative;
      padding-left: 26px;
      font-weight: 700;
      font-size: 28px;
      line-height: 34px;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 12px;
        width: 10px;
        height: 10px;
        background-color: $color-accent;
      }

      &:not(:last-of-type) {
        margin-bottom: 10px;
      }
    }

    .btn {
      padding-top: 22px;
      padding-bottom: 21px;
      width: 100%;
      max-width: 240px;
      font-size: 16px;
    }
  }
}

//
// SPECIAL BLOCK
//

.special {
  position: relative;
  margin-bottom: 7px;

  &__wrapper {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }
}

.special-item {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-end;
  padding-bottom: 40px;
  width: 570px;
  height: 280px;
  text-decoration: none;
  color: $color-black-primary;
  background-size: cover;

  &::before {
    @include triangle(to left top, 50px, 50px, $color-white);

    content: "";
    position: absolute;
    left: 0;
    top: 0;
  }

  &:nth-of-type(2n) {
    &::before {
      @include triangle(to right bottom, 50px, 50px, $color-white);

      left: auto;
      right: 0;
      top: auto;
      bottom: 0;
    }
  }

  &__name {
    position: relative;
    display: flex;
    align-items: center;
    overflow: hidden;
    padding-right: 50px;
    padding-left: 20px;
    width: 100%;
    height: 80px;
    max-width: 375px;
    text-transform: uppercase;

    &::before {
      content: "";
      position: absolute;
      left: -40px;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: $color-accent-yellow;
      transform: skewX(-45deg);
    }

    h5 {
      position: relative;
      z-index: 1;
      margin: 0;
      line-height: 24px;
    }
  }
}

//
// SERVICES BLOCK
//

.services-nav {
  &__list {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__link {
    position: relative;
    display: block;
    overflow: hidden;
    width: 270px;
    height: 200px;
    text-decoration: none;
    background-color: $color-gray-decor;

    &::before {
      @include triangle(to left top, 40px, 40px, $color-white);

      content: "";
      position: absolute;
      left: -1px;
      top: -1px;
      z-index: 2;
    }

    &::after {
      @include triangle(to right bottom, 40px, 40px, $color-white);

      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: 2;
    }

    &--evacuator {
      .services-nav__pic {
        display: flex;
        align-items: center;
        padding: 45px;
      }
    }

    @include desktop {
      &:hover,
      &:focus {
        .services-nav__name {
          color: $color-white;
          background-color: $color-accent;
          transition: color 0.3s, background-color 0.3s;
        }
      }
    }
  }

  &__name {
    position: relative;
    z-index: 1;
    display: block;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 50px;
    width: 100%;
    font-weight: 700;
    font-size: $fz-small;
    color: $color-black-primary;
    background-color: $color-accent-yellow;
    transition: color 0.3s, background-color 0.3s;
  }

  &__pic {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    img {
      max-width: 100%;
      max-height: 100%;
    }
  }

  &__description {
    position: absolute;
    bottom: 15px;
    width: 100%;
    text-align: center;

    &-title {
      position: relative;
      display: inline-block;
      margin: 0;
      margin-bottom: 7px;
      padding: 3px 11px;
      color: $color-white;
      background-color: $color-accent;
      transform: skewX(-20deg);

      span {
        display: block;
        transform: skewX(20deg);
      }
    }

    &-phone {
      margin: 0;
      font-size: 16px;
      color: $color-black-primary;
    }

    &-bg {
      position: absolute;
      left: 0;
      bottom: -4px;
      width: 100%;
      font-weight: 700;
      font-size: 40px;
      text-align: center;
      color: rgba($color-black-primary, 0.03);
    }
  }
}

.services-grid {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  padding-top: 30px;

  &__triangles {
    position: relative;
    left: 0;
    width: 100%;

    &--top {
      top: 0;
      height: 16px;

      &::before {
        @include triangle(to top left, 10px, 10px, $color-accent-yellow);

        content: "";
        position: absolute;
        left: 2px;
        top: 2px;
        transition: border-color 0.3s;
      }

      &::after {
        @include triangle(to top right, 10px, 10px, $color-accent-yellow);

        content: "";
        position: absolute;
        right: 2px;
        top: 2px;
        transition: border-color 0.3s;
      }
    }

    &--bottom {
      bottom: 0;
      height: 22px;

      &::before {
        @include triangle(to bottom left, 10px, 10px, $color-accent-yellow);

        content: "";
        position: absolute;
        left: 2px;
        bottom: 2px;
        transition: border-color 0.3s;
      }

      &::after {
        @include triangle(to bottom right, 10px, 10px, $color-accent-yellow);

        content: "";
        position: absolute;
        right: 2px;
        bottom: 2px;
        transition: border-color 0.3s;
      }
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    // padding-top: 16px;
    // padding-bottom: 22px;
    width: 170px;
    height: 170px;
    text-decoration: none;
    color: $color-black-primary;
    background-color: $color-gray-decor;
    transition: background-color 0.3s;

    &:last-child {
      justify-content: center;
    }

    @include desktop {
      &:hover,
      &:focus {
        background-color: $color-accent-yellow;
        transition: background-color 0.3s;

        .services-grid__triangles {
          &--bottom {
            &::before {
              @include triangle(to bottom left, 10px, 10px, $color-white);

              transition: border-color 0.3s;
            }

            &::after {
              @include triangle(to bottom right, 10px, 10px, $color-white);

              transition: border-color 0.3s;
            }
          }

          &--top {
            &::before {
              @include triangle(to top left, 10px, 10px, $color-white);

              transition: border-color 0.3s;
            }

            &::after {
              @include triangle(to top right, 10px, 10px, $color-white);

              transition: border-color 0.3s;
            }
          }
        }
      }
    }
  }

  &__name {
    padding: 0 14px;
    font-weight: 500;
    font-size: $fz-small;
    line-height: 18px;
    text-align: center;

    &--tdu {
      text-decoration: underline;
    }
  }

  &__icon {
    width: 72px;
    height: 72px;
  }
}

//
// ADVANTAGES BLOCK
//

.advantages {
  padding-top: 80px;

  .section-title {
    position: relative;
    padding-top: 30px;
    text-align: left;

    &__title {
      margin-bottom: 0;
    }

    &__subtitle {
      left: 0;
      bottom: 60px;
      width: 100%;
      text-align: left;
      transform: none;
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.advantages-item {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  justify-content: flex-start;
  justify-self: flex-end;
  margin-right: -12px;
  margin-bottom: 35px;
  padding-top: 14px;
  padding-bottom: 10px;
  width: 460px;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    display: block;
    width: 400px;
    height: 100%;
    background-color: $color-gray-decor;
    transform: skewX(-33deg);
  }

  &:nth-of-type(2n) {
    justify-self: flex-start;
    margin-left: 45px;
  }

  &__icon {
    margin-right: 34px;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 5px;
  }

  &__text {
    margin: 0;
    max-width: 320px;
    line-height: 22px;
  }
}

//
// MAIN-CALLBACK BLOCK
//

.main-callback,
.callback-form {
  position: relative;
  margin-top: 78px;
  margin-bottom: 100px;
  border-top: 12px solid $color-accent;
  padding: 0 165px;
  padding-bottom: 74px;
  box-shadow: 0 24px 40px rgba(0, 0, 0, 0.12);
  text-align: center;
  background:
    url('../images/mcallback_bg_1.png') top left no-repeat,
    url('../images/mcallback_bg_2.png') bottom right no-repeat;
  background-color: $color-white;

  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: -25%;
    z-index: -1;
    display: block;
    width: 230px;
    height: 420px;
    background-color: $color-accent-yellow;
    transform: translateX(-58%) skewX(-35deg);
  }

  &__title {
    margin-top: 34px;
    margin-bottom: 15px;
    width: 100%;
  }

  &__subtitle {
    margin-top: 0;
    margin-bottom: 30px;
  }

  &__fields {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    width: 100%;

    .btn {
      width: 100%;
      max-width: 240px;
      font-size: 16px;
    }
  }

  &__footer {
    padding-top: 10px;
    width: 100%;
    font-size: 12px;
    text-align: left;

    a {
      color: $color-black-primary;

      @include desktop {
        &:hover,
        &:focus {
          text-decoration: underline;
        }
      }
    }
  }

  &__privacy {
    max-width: 350px;
  }

  &--questions {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding-bottom: 55px;

    &::after {
      height: calc(100% + 120px);
      top: -18%;
      left: 52%;
    }

    .callback-form__title {
      margin-top: 42px;
      margin-bottom: 25px;
      font-size: 22px;
    }

    .callback-form__fields {
      flex-flow: column nowrap;
      width: 280px;

      .field {
        margin-bottom: 16px;
      }
    }

    .callback-form__footer {
      display: flex;
      flex-flow: row nowrap;
      justify-content: space-between;
      padding-top: 5px;

      .btn {
        font-size: 16px;
        padding: 22px;
        width: 100%;
        max-width: 240px;
      }
    }

    .callback-form__textarea {
      width: calc(100% - 280px - 20px);
      height: 140px;

      .field__input {
        height: 140px;
      }
    }
  }
}

//
// CONTACTs-US BLOCK
//

.contacts-us {
  position: relative;

  .section-title {
    padding-top: 60px;

    &__title {
      margin-bottom: 45px;
    }
  }

  &__wrapper {
    display: flex;
    flex-flow: row nowrap;
  }

  &__adresses {
    padding-bottom: 60px;
    width: 50%;

    ul {
      list-style: none;
      margin: 0;
      padding-left: 0;
      width: 65%;
    }

    li {
      position: relative;
      padding-left: 37px;
      line-height: 20px;

      &:not(:last-of-type) {
        margin-bottom: 16px;
      }
    }

    a {
      font-size: 18px;
      text-decoration: none;
      color: $color-black-primary;
    }

    svg {
      position: absolute;
      left: 0;
      top: -3px;
      width: 22px;
      height: 22px;
    }
  }

  &__title {
    margin-top: 22px;
    margin-bottom: 18px;
  }

  &__map {
    position: absolute;
    right: 0;
    top: 120px;
    width: 49%;
    height: 540px;
  }

  &__video {
    position: relative;
    overflow: hidden;
    padding-top: 30px;
    padding-bottom: 44%;
    width: 93%;
    height: 0;

    iframe {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }

    @media (max-width: 767px) {
      padding-bottom: 56.25%;
      width: 100%;
    }
  }
}

.services {
  &--main {
    padding-bottom: 100px;

    .services-grid {
      justify-content: flex-start;

      &__item {
        margin-right: 30px;
        width: 100%;
        max-width: 270px;

        &:nth-of-type(4n) {
          margin-right: 0;
        }

        &:last-child {
          justify-content: space-between;
        }

        // &:not(:last-child),
        // &:not(:nth-of-type(4)) {
        //   margin-right: 30px;
        // }
      }
    }
  }
}