.reviews {
  position: relative;
  padding-bottom: 84px;

  .section-title__title {
    margin-bottom: 20px;
  }

  &__form {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;

    p {
      margin-top: 0;
      padding-right: 18%;
      width: calc(100% - 240px);
      line-height: 20px;
    }

    .btn {
      padding: 20px 10px;
      width: 240px;
      font-size: 16px;
    }
  }
}

.reviews-list {
  position: relative;
  margin-top: 52px;
  padding-left: 0;

  &__item {
    display: flex;
    flex-flow: row nowrap;
    margin-bottom: 32px;
  }

  &__info {
    display: flex;
    flex-flow: column nowrap;
    width: 26%;

    &-name {
      margin-bottom: 10px;
      font-size: 18px;
      line-height: 22px;
    }

    &-car {
      margin-bottom: 20px;
    }

    &-date {
      font-size: $fz-small;
      color: #999;
    }
  }

  &__review {
    position: relative;
    border-top: 2px solid $color-accent-yellow;
    padding-top: 30px;
    padding-bottom: 20px;
    padding-left: 50px;
    width: 74%;
    font-size: 16px;
    line-height: 20px;
    background:
      url('../images/review_bg.png') bottom right no-repeat;
    background-color: $color-gray-decor;

    span {
      position: relative;
      z-index: 1;
    }

    &-bg {
      position: absolute;
      left: 0;
      top: -2px;
      border-top: 2px solid $color-accent-yellow;
      width: 20px;
      height: calc(100% + 2px);
      background-color: $color-white;

      &::before {
        @include triangle(to top right, 20px, 20px, $color-gray-decor);

        content: "";
        position: absolute;
        left: 0;
        top: 0;
      }
    }

    &-quote {
      position: absolute;
      left: 40px;
      top: 20px;
      width: 35px;
      height: 30px;
    }
  }

  &__show-more {
    display: block;
    margin-top: 3px;
    font-size: $fz-small;
    text-decoration: none;
    color: #999;

    @include desktop {
      &:hover,
      &:focus {
        text-decoration: underline;
      }
    }
  }

  &__is-hidden {
    font-size: 0;
    opacity: 0;
    visibility: visible;
  }
}
